import { createSlice } from "@reduxjs/toolkit";

let initialState = {
    products : [],
    product : {}
}

export let productSlice = createSlice({
    name : 'product',
    initialState,
    reducers : {
        fetchingProducts : (state,{payload}) => {
            state.products = payload
        },
        selectingProduct : (state,{payload}) => {
            state.product = payload;
        },
        deselectingProducts : (state,{payload}) => {
            return state;
        }
    }
})

export let {fetchingProducts,selectingProduct,deselectingProducts} = productSlice.actions;

export let PRODUCTS = state => state.productReducer.products;
export let PRODUCT = state => state.productReducer.product;

export default productSlice.reducer