import * as React from 'react';
import { useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import IconButton from '@mui/material/IconButton';
import Typography from '@mui/material/Typography';
import { Button, Grid } from '@mui/material';
import { Container } from '@mui/system';
import { useDispatch, useSelector } from 'react-redux';
import { addingCart, CARTPRODUCT, deletingCart, increasingCart, reducingCart } from '../../features/carts/cartSlice';

export default function MyCart() {

  let Added_Products = useSelector(CARTPRODUCT)
  let dispatch = useDispatch()
  console.log(Added_Products)

 let Calculation = (id,counter) => {
  if(counter == 'addition'){
    dispatch(increasingCart(id))
  }else if(counter == 'sub'){
    dispatch(reducingCart(id))
  }else{
    dispatch(deletingCart(id))
  }
 }

  return (
    <div className='w-full'>
<div className='mt-10'>
  {
    Added_Products.length !== 0 ? 
    
<Container>        
{
  Added_Products.map((product,key) => (
              
    <Card key={key} className='w-[100%]' sx={{marginBottom : '20px' ,alignItems : 'center', display: 'flex' , backgroundColor : 'rgb(242, 242, 242)' ,paddingRight : '96px'}}>
            
    <CardMedia
      component="img"
      sx={{ width: 31 }}
      image={`${product.img}`}
    />
      <CardContent className='w-[200px]' sx={{ flex: '1 0 auto' }}>
        <Typography component="div" className='text-[13px] font-[600]' variant="subtitle3" style={{maxWidth : '100px'}}>
          {product.title}
        </Typography>
        <Typography className='text-12px font-[400]' variant="subtitle3" color="text.secondary" component="div">
         {product.category}
        </Typography>
      </CardContent>
      <div className='pr-5 flex flex-col items-center gap-3'>
        <Button className='w-[10px]' onClick={()=>Calculation(product.id,'addition')} style={{backgroundColor : 'rgb(38, 50, 68)' , color : 'white', fontSize : 'small' }}>+</Button>
        <Typography> {product.qty}
        </Typography>
        <Button className='w-[10px]' onClick={()=>Calculation(product.id,'sub')} style={{backgroundColor : 'rgb(38, 50, 68)' , color : 'white', fontSize : 'small' }}>- </Button>
      </div>
      <Typography style={{fontWeight : 'bold'}} variant="subtitle1" component='div'>$ {parseFloat(product.price) * parseFloat(product.qty)}</Typography>
<Button onClick={()=>Calculation(product.id,'del')} style={{fontSize : 'large' , color : 'black',marginLeft : '5px'}}>X</Button>
  </Card>   
  ))
}       
              </Container>
              :<div className='w-fit mx-auto mt-40 mb-80 text-center text-white font-bold text-xl h-full'>No data yet</div>
  }
</div>
    </div>
  );
}