import * as React from 'react';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { useParams } from 'react-router';
import { api } from '../../api/api';
import { useDispatch, useSelector } from 'react-redux';
import { PRODUCT, selectingProduct } from '../../features/products/productSlice';
import { addingCart } from '../../features/carts/cartSlice';
import ClockLoader from 'react-spinners/ClockLoader';

export default function MyDetail() {

let {productId} = useParams();
let [isLoading,setIsLoading] = React.useState(false)
let dispatch = useDispatch()
let productLists =useSelector(PRODUCT)

let handleCartProduct = (img,id,title,price,category ) => {
  dispatch(addingCart({img,id,title,price,category}))
}

let selectProduct = async () => {
  setIsLoading(true)
let res = await api.get(`/products/${productId}`)
dispatch(selectingProduct(res.data))
console.log(res.data,res)
setIsLoading(false)
}

React.useEffect(()=>{
  selectProduct()
},[])

  return (
   <div className='w-full h-full mt-7'>
    {
      isLoading === false ? 
      
    <div>
    <div className='w-fit h-fit mx-auto'>
    <img src={`${productLists.image}`} className='md:w-60 lg:w-60 w-30 h-auto' />
    </div>
    <div className='w-fit h-fit mx-auto mt-12'>
    <Button onClick={()=>handleCartProduct(productLists.image,productLists.id,productLists.title,productLists.price,productLists.category)} style={{backgroundColor : 'white', color : 'black'}}> Add to cart</Button>
    </div>
    <div className='md:w-3/4 lg:w-3/4 w-4/5 h-fit mx-auto mt-12 bg-stone-100 px-1 py-8 md:px-10 lg:py-8 lg:px-10 rounded-sm'>
    <h5 className='text-center mb-9 lg:text-inherit text-sm md:text-inherit' style={{fontWeight : 'bold'}}>{productLists.title}</h5>
    <h5 className='mb-5 lg:text-inherit text-sm md:text-inherit'>Price - $ {productLists.price}</h5>
    <h5 className='mb-6 lg:text-inherit text-sm md:text-inherit'>Category -  {productLists.category}</h5>
      <div className='font-bold mb-2 text-sm text-justify'>Description</div>
    <Typography>{productLists.description}</Typography>
    </div>
  </div>
  : 
  <div className='w-fit mx-auto mt-60  text-white font-bold text-md flex items-center'>
              <ClockLoader
              className='mr-4'
  color="#ffffff"
  size={60}
  speedMultiplier={3.5}
/>
Loading...
            </div>
    }
   </div>
  );
}