import React from 'react'
import MyProducts from '../products/MyProducts'

const MyHome = () => {
  return (
    <div>
        <div className='lg:flex md:flex block w-fit mx-auto mt-10 text-center md:text-inherit lg:text-inherit text-white'>
        <p className='sm:text-md text-sm lg:text-xl font-bold text-white'>Most popular , branded outfits and other accessories are available in</p><p className='ml-2 sm:text-md text-sm lg:text-xl font-bold text-red-600'>Junior.com</p>
        </div>
        <MyProducts/>
    </div>
  )
}

export default MyHome