import { Route, Routes } from "react-router";
import MyCart from "./components/cart/MyCart";
import MyDetial from "./components/detail/MyDetial";
import MyFav from "./components/favourite/MyFav";
import MyFooter from "./components/footer/MyFooter";
import MyHome from "./components/home/MyHome";
import MyNavbar from "./components/navbar/MyNavbar";
import NotFound from "./components/notfound/NotFound";

function App() {
  return (
  <div className="w-full h-full" style={{backgroundColor : 'rgb(30, 32, 40)'}}>
  <MyNavbar/>
  <Routes>
    <Route path="/" element={<MyHome/>}/>
    <Route path="/products" element={<MyHome/>}/>
    <Route path="product/:productId" element={<MyDetial/>}/>
    <Route path="cart" element={<MyCart/>}/>
    <Route path="/favourites" element={<MyFav/>}/>
    <Route path="*" element={<NotFound/>}/>
  </Routes>
  <MyFooter/>
  </div>
  );
}

export default App;
