import { Container, Grid } from '@mui/material'
import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { ClockLoader } from 'react-spinners'
import { api } from '../../api/api'
import { fetchingProducts, PRODUCTS } from '../../features/products/productSlice'
import MyCard from '../cards/MyCard'

const MyProducts = () => {

  let dispatch = useDispatch();
  let products = useSelector(PRODUCTS)
  let [isLoading,setIsLoading] = useState(false);

 let fetchProducts = async () => {
  setIsLoading(true)
  let res = await api.get('/products')
  console.log(res.data)
 dispatch(fetchingProducts(res.data))
 setIsLoading(false)
 }

 useEffect(()=>{
fetchProducts();
 },[])

  return (
        <Container>
          {
            isLoading === false ? 
            <MyCard products={products}/>
            : <div className='w-fit mx-auto mt-40  text-white font-bold text-md flex items-center'>
              <ClockLoader
              className='mr-4'
  color="#ffffff"
  size={60}
  speedMultiplier={3.5}
/>
Loading...
            </div>
          }
</Container>
    
  )
}

export default MyProducts