import { createSlice } from "@reduxjs/toolkit";

let initialState = {
    products : []
}

export let cartSlice = createSlice({
    name : 'cart',
    initialState,
    reducers : {
        addingCart : (state,{payload}) => {
            let  filteredProduct = state.products.filter(product => product.id == payload.id)

            if(filteredProduct.length < 1){                
            state.products.push({...payload,qty:1})
            }
            
            else{
                let filteredProductList = state.products.map((product)=> {
                    if(product.id == payload.id){
                        product.qty ++
                    }
                    return product;
                })
               state.products = filteredProductList;
            }
            
        },
        increasingCart : (state,{payload}) => {
            let filteredProductList = state.products.map((product)=> {
                if(product.id == payload){
                    product.qty ++
                }
                return product;
            })
           state.products = filteredProductList;
        },
        reducingCart : (state,{payload})=>{
            let filteredProductList = state.products.filter((product)=> {
             if(product.qty !== 1 && product.id == payload){
                     product.qty --;
                     return product;
                 }else if( product.qty == 1 && product.id !== payload){
                     return product;
                 }
            })
            state.products = filteredProductList;
         },
         deletingCart : (state,{payload}) => {
            let filteredProductList = state.products.filter((product) => {
                if(product.id !== payload){
                    return product
                }
            })
            state.products = filteredProductList
        }
    }

})

export let {addingCart,reducingCart,increasingCart,deletingCart} = cartSlice.actions
export let CARTPRODUCT = state => state.cartReducer.products
export default cartSlice.reducer;