import * as React from 'react';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Button from '@mui/material/Button';
import Typography from '@mui/material/Typography';
import { Badge, Grid, IconButton } from '@mui/material';
import FavoriteIcon from '@mui/icons-material/Favorite';
import AddShoppingCartIcon from '@mui/icons-material/AddShoppingCart';
import { Link } from 'react-router-dom';

export default function MyCard({products}) {
  return (
<div className='grid gap-4 md:gap-16 sm:gap-14 lg:gap-16  md:grid-cols-3 lg:grid-cols-4 sm:grid-cols-2 grid-cols-1 mt-20 mx-auto w-fit ' >
  
      {products.length !== 0 ?
        products.map((product,key)=> (
          
      <Card key={key} sx={{ maxWidth: 345 }}>
   <CardMedia
          sx={{ height: 140 }}
          image={`${product.image}`}
          title="green iguana"
        />
        <CardContent>
          <Typography gutterBottom variant="h7" style={{fontWeight : 'bold'}} component="div">
           {product.title.substring(0,16) + '...'}
          </Typography>
          <Typography variant="body2" color="text.secondary">
            $ {product.price}
          </Typography>
          <Typography variant="h7" color="text.secondary">
            {product.category}
          </Typography>
        </CardContent>
        <CardActions>
       <Link to={`product/${product.id}`}>
       <Button size="small" style={{color : 'white', backgroundColor : 'rgb(33, 32, 32)'}}>
              <AddShoppingCartIcon style={{fontSize : 'medium' , marginRight : '3px'}}/>
              Add</Button>
       </Link>
        </CardActions>
      </Card>
        ))
        :
          <h1>Loading...</h1>
      }
</div>
         
  );
}